* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  background-color: #282c34;
}

.center {
  display: flex;
  width: 100%;
  height: 100vh;
  justify-content: center;
  align-items: center;
  
  font-size: calc(10px + 2vmin);
  color: white;
}

.centered {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  text-align: center;
  
  font-size: calc(10px + 2vmin);
  color: white;
}

.box {
  width: 300px;
}

input[type="number"] {
  background-color: #282c34;
  border-radius: 5px;
  border: 2px solid #343842;
  
  font-size: calc(10px + 2vmin);
  color: white;
  width: 50%;
  text-align: center;
}

input[type="submit"], button {
  margin-top: 1rem;
  background-color: #2e6175;
  border: 1px solid #343842;
  color: white;
  padding: 5px;
  text-align: center;
  width: 100%;
  max-width: 250px;
  font-size: calc(5px + 2vmin);
  border-radius: 5px;
}

span {
  position: absolute;
  top: 0;
}

.error {
  border: 3px solid rgb(223, 86, 86) !important
}

.correct {
  animation: border-pulse 1s ease-out;
}

@keyframes border-pulse {
  0%   { border-color: rgba(0, 255, 0, 0); }
  50%  { border-color: rgb(112, 255, 112); }
  100%   { border-color: rgba(0, 255, 0, 0); }
}